<template>
  <div class="wrapper">
    <router-view></router-view>
    <Tabbar v-model="active" class="footer"  @change="onChange">
      <TabbarItem icon="home-o" >首页</TabbarItem>
      <TabbarItem icon="search">随手拍</TabbarItem>
      <TabbarItem icon="add-o">举报</TabbarItem>
      <TabbarItem icon="friends-o">我的</TabbarItem>
    </Tabbar>
  </div>
</template>

<script>
import { Tabbar, TabbarItem } from 'vant';
import {ref} from 'vue';
import { useRouter } from 'vue-router';
export default {
     name: "index",
     setup(){
         const active = ref(0);
         const hash =location.hash;
         switch (hash){
             case '#/home':
                 active.value = 0;
                 break;
             case  '#/clap':
                 active.value = 1;
                 break;
             case  '#/report':
                  active.value = 2;
                 break;
             case '#/mine':
                  active.value = 3;
                  break;
             default:
                  active.value = 0;
         }

         const router = useRouter();
         const  onChange = (i) => {
             switch (i){
                  case 0:
                    router.push('/home');
                    break;
                  case 1:
                    router.push('/clap');
                    break;
                  case 2:
                    router.push('/report');
                    break;
                  case 3:
                    router.push('/mine');
                    break;
             }

         }
         return {
            active,
            onChange
         }
     },
     components:{
       Tabbar,
       TabbarItem
     },
     methods:{
         change(){
             this.$router.push('/clap')
         }
     }
}
</script>

<style scoped lang="scss">
     @import "../assets/styles/mixin";
     .wrapper{
         font-size: px2rem(16);
         width: 100%;
         height: 100%;
     }

     .footer{
         height: px2rem(50);
         width: 100%;
         position: absolute;
         bottom:0px;
     }
</style>